.hero__content {
	h1 {
		margin-bottom: 0;
	}
}

.hero-ctn {
	a,
	a:visited,
	span {
		color: #b388dd;
		transition: all 0.3s ease-in-out;
	}

	.post-tags .post-tag,
	.post-tags a.post-tag,
	.post-tags a.post-tag:visited {
		color: map-get($map: $colors, $key: white);

		&:hover {
			border-color: map-get($map: $colors, $key: white);
		}
	}
}

.hero__buttons {
	margin-top: 50px;
}

.post-tags {
	display: flex;
	flex-wrap: wrap;
	gap: 8px;
	line-height: 1;

	.post-tag,
	a.post-tag,
	a.post-tag:visited {
		border: 1px solid var(--ed_ugray);
		border-radius: 100px;
		padding: 8px 24px;
		box-sizing: border-box;
		@include font(st, med);
		text-decoration: none;
		color: map-get($map: $colors, $key: black);
		transition: all 0.3s ease-in-out;

		&:hover {
			border-color: map-get($map: $colors, $key: black);
		}
	}

	a.post-tag,
	a.post-tag:visited {
		&:hover {
			border-color: map-get($map: $colors, $key: white);
		}
	}
}

.hero-bottom-content {
	width: 33%;
}

.hero-bottom-image {
	aspect-ratio: 1 / 0.6;
	line-height: 0.65;
	width: 57.5%;
}

.hero-section {
	padding-top: 102px;
	box-sizing: border-box;
}

.hero--home {
	.hero__content {
		padding: var(--ed_space_6) 0;
		box-sizing: border-box;
	}
}
